import React, { useState, useEffect } from 'react';
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";

import avatar from "assets/img/faces/marc.jpg";

import axios from "axios";
// const axios = require('axios').default;

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);

export default function UserProfile(props) {
  const [is_challenged, setChallenged] = useState(false);
  const [is_inited, setInited] = useState(false)
  const [is_promoted, setPromoted] = useState(false)
  
  const [name, setName] = useState("")
  const [age, setAge] = useState("")
  const [ip, setIp] = useState("")
  const [port, setPort] = useState("")
  const [connected, setConnected] = useState("")
  const [schemaName, setSchemaName] = useState("")
  // const [key, setKey] = useState("")

  const classes = useStyles();

  useEffect(() => {
    getStatus()
  });

  
  function getStatus(){
    axios.get('/api/status').then(res => {
      if (is_inited != res.data.is_inited){
        setInited(res.data.is_inited);
      }
      if (is_promoted != res.data.is_promoted){
        setPromoted(res.data.is_promoted)
      }
      if (is_challenged != res.data.is_challenged){
        setChallenged(res.data.is_challenged)
      }
      if (connected.ip != res.data.connected_ip) {
        setConnected({ip: res.data.connected_ip, port: res.data.connected_port, id: res.data.connected_id})
      }
      console.log(res.data)
    })
  }

  function changeHandler(ev) {
    switch (ev.target.id) {
      case "name":
        setName(ev.target.value)
        break
      case "age":
        setAge(ev.target.value)
        break
      case "ip":
        setIp(ev.target.value)
        break
      case "port":
        setPort(ev.target.value)
        break
      case "schemaName":
        setSchemaName(ev.target.value)
        break
    }
    // console.log(event.target.value)
  }

  function sendCredReq(){
    axios.post('/api/credential/request', {
      host: connected.ip, 
      port: connected.port,
      schemaID: schemaName,
      info: {
        name,
        age
      }
    }).then(res => {
      // setInited(res.data.is_inited);
      // setPromoted(res.data.is_promoted)
      console.log(res.data)
      getStatus()
    })
  }
  
  function sendChReq(){
    axios.post("/api/challenge/request", {host: ip, port}).then(res => {
      console.log(res.data)
      getStatus()
    })
  }

  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={10}>
            <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Send Challenge Request</h4>
              <p className={classes.cardCategoryWhite}>Complete the form</p>
            </CardHeader>
            {is_challenged? 
            <CardBody>Challenge completed: <br/>
                      ip: {connected.ip} <br/> port: {connected.port} <br/> id: {connected.id} 
            </CardBody>:""}
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    labelText="Ip"
                    id="ip"
                    formControlProps={{
                      fullWidth: true
                    }}
                    onChange={changeHandler}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    labelText="Port"
                    id="port"
                    formControlProps={{
                      fullWidth: true
                    }}
                    onChange={changeHandler}
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
            {/*is_challenged? "":*/
            <CardFooter>
              <Button color="primary" onClick={sendChReq}>Send</Button>
            </CardFooter>
            }
          </Card>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Send Credential Request</h4>
              <p className={classes.cardCategoryWhite}>Complete the form</p>
            </CardHeader>
            <CardBody>
              <GridContainer>
              <GridItem xs={12} sm={12} md={4}>
              <CustomInput
                labelText="Schema name"
                id="schemaName"
                formControlProps={{
                  fullWidth: true
                }}
                onChange={changeHandler}
              />
            </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    labelText="Name"
                    id="name"
                    formControlProps={{
                      fullWidth: true
                    }}
                    onChange={changeHandler}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    labelText="Age"
                    id="age"
                    formControlProps={{
                      fullWidth: true
                    }}
                    onChange={changeHandler}
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
            <CardFooter>
              <Button color="primary" onClick={sendCredReq}>Send</Button>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}
