import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import Quote from "components/Typography/Quote.js";
import Muted from "components/Typography/Muted.js";
import Primary from "components/Typography/Primary.js";
import Info from "components/Typography/Info.js";
import Success from "components/Typography/Success.js";
import Warning from "components/Typography/Warning.js";
import Danger from "components/Typography/Danger.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Button from "components/CustomButtons/Button.js"
import Table from "components/Table/Table.js";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import { useState, useEffect } from 'react';
import axios from 'axios';


const styles = {
  typo: {
    paddingLeft: "25%",
    marginBottom: "40px",
    position: "relative"
  },
  note: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    bottom: "10px",
    color: "#c0c1c2",
    display: "block",
    fontWeight: "400",
    fontSize: "13px",
    lineHeight: "13px",
    left: "0",
    marginLeft: "20px",
    position: "absolute",
    width: "260px"
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);

export default function TypographyPage() {

  const [is_inited, setInited] = useState(false)
  const [is_promoted, setPromoted] = useState(false)
  const [name, setName] = useState("")
  const [key, setKey] = useState("")

  const classes = useStyles();

  function getStatus(){
    axios.get('/api/status').then(res => {
      setInited(res.data.is_inited);
      setPromoted(res.data.is_promoted)
      if (name == ""){
        setName(res.data.name)
      }
      if (key == ""){
        setKey(res.data.key)
      }
      console.log(res.data)
    })
  }

  useEffect(()=>{
    getStatus()
  });

  function initHandler() {
    axios.post('/api/init', {name, key}).then(res => {
      // setInited(res.data.is_inited);
      // setPromoted(res.data.is_promoted)
      console.log(res.data)
      getStatus()
    })
  }

  function promoteHandler(){
    axios.post('/api/promote').then(res => {
      console.log(res.data)
      getStatus()
    })
  }

  function changeHandler(ev) {
    switch (ev.target.id) {
      case "name":
        setName(ev.target.value)
        break
      case "key":
        setKey(ev.target.value)
        break
    }
    // console.log(event.target.value)
  }

  return (
    <Card>
      <CardHeader color="primary">
        <h4 className={classes.cardTitleWhite}>Your wallet Status</h4>
      </CardHeader>
    <CardBody>
    {is_promoted? "You have been promoted": ""}
    {is_inited? 
        <Table
        tableHeaderColor="primary"
        tableHead={["Name", "key"]}
        tableData={[
          [name, key],
        ]}
        button1Name={is_promoted? "" : "Promote"}
        button1Handler= {promoteHandler}
      />:
      <GridContainer>
      <GridItem xs={12} sm={12} md={6}>
        <CustomInput
          labelText="Name"
          id="name"
          formControlProps={{
            fullWidth: true
          }}
          onChange={changeHandler}
        />
      </GridItem>
      <GridItem xs={12} sm={12} md={6}>
        <CustomInput
          labelText="Key"
          id="key"
          formControlProps={{
            fullWidth: true
          }}
          onChange={changeHandler}
        />
      </GridItem>
      <Button color="primary" onClick={initHandler}>Init</Button>
    </GridContainer>
    } 
    </CardBody>
    </Card>
  );
}
