import React, { useState, useEffect } from 'react';
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import axios from "axios";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};

const useStyles = makeStyles(styles);

export default function TableList() {
  const [is_promoted, setPromoted] = useState(false)
  const [schemas, setSchemas] = useState([])
  const [schemaName, setSchemaName] = useState("")
  const [creds, setCreds] = useState([])

  useEffect(() => {
    getStatus()
    getSchema()
    getCreds()
  });

  
  function getStatus(){
    axios.get('/api/status').then(res => {
      if (res.data.is_promoted != is_promoted) {
        setPromoted(res.data.is_promoted)
      }
      // console.log(res.data)
    })
  }

  function getSchema(){
    axios.get('/api/schema').then(res => {
      if (res.data.length != schemas.length) {
        setSchemas(res.data)
      }
    })
  }

  function getCreds(){
    axios.get('/api/credential').then(res=> {
      let tmp = []
      // tmp.push
      setCreds(res.data)

      // console.log(res.data)
    })
  }

  function createSchema(){
    axios.post("/api/schema", {
      name: schemaName,
      attributes: ["name", "age"]
    }).then(res => {
      getSchema()
    })
  }

  function changeHandler(ev) {
    switch (ev.target.id) {
      case "schemaName":
        setSchemaName(ev.target.value)
        break
    }
  }


  const classes = useStyles();
  let table_data = []
  for(let schema of schemas){
    table_data.push([schema.id, schema.name].concat(schema.attributes))
  }
  let cred_data = []
  for(let cred of creds){
    cred_data.push([cred.targetId, cred.schemaName, cred.auth])
  }
  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Credentials</h4>
            <p className={classes.cardCategoryWhite}>
                credential
            </p>
          </CardHeader>
          <CardBody>
            <Table
              tableHeaderColor="primary"
              tableHead={["target ID", "Schema name", "auth"]}
              tableData={cred_data}
              buttonName1="verify"
              buttonName2="send"
            />
          </CardBody>
        </Card>
      </GridItem>
      <GridItem xs={12} sm={12} md={12}>
        <Card plain>
          <CardHeader plain color="primary">
            <h4 className={classes.cardTitleWhite}>
              Schema
            </h4>
            <p className={classes.cardCategoryWhite}>
                schema
            </p>
          </CardHeader>
            <CardBody>
            <GridContainer>
            <GridItem xs={12} sm={12} md={4}>
              <CustomInput
                labelText="Scehma Name"
                id="schemaName"
                formControlProps={{
                  fullWidth: true
                }}
                onChange={changeHandler}
              />
            </GridItem>
            {/*<GridItem xs={12} sm={12} md={4}>
              <CustomInput
                labelText="name"
                id="port"
                formControlProps={{
                  fullWidth: true
                }}
                onChange={changeHandler}
              />
              </GridItem>
              <GridItem xs={12} sm={12} md={4}>
              <CustomInput
                labelText="age"
                id="port"
                formControlProps={{
                  fullWidth: true
                }}
                onChange={changeHandler}
                inputProps ={{"readonly": true}}
                labelText="age"
              />
              </GridItem>*/}
          </GridContainer>
          <Button color="primary" onClick={createSchema}> </Button>
            <Table
              tableHeaderColor="primary"
              tableHead={["ID", "Name"]}
              tableData={table_data}
            />
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
}
